<template>
    <div class="container text-center">
        <h1>Notas</h1>
        <hr>

        <form @submit.prevent="agregarNota()" v-if="!editando.state">
            <h3>Agregar Nueva nota</h3>
            <input type="text" class="form-control my-2" placeholder="Nombre" v-model="nota.nombre">
            <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="nota.descripcion">
            <b-button type="submit" class="btn-block btn-success">Agregar</b-button>
        </form>

        <form @submit.prevent="submitEditarNota(editando.id)" v-if="editando.state">
            <h3>Editar nota {{editando.id}}</h3>
            <input type="text" class="form-control my-2" placeholder="Nombre" v-model="nota.nombre">
            <input type="text" class="form-control my-2" placeholder="Descripcion" v-model="nota.descripcion">
            <b-button type="submit" class="btn-block btn-warning">Editar</b-button>
            <b-button class="btn-block btn-secondary" @click="editando.state = false">Cancelar</b-button>
            
        </form>

        <hr>
        <table class="table">
            <thead class="thead-dark">
                <tr>
                    <th scope="col">ID</th>
                    <th scope="col">Nombre</th>
                    <th scope="col">Descripcion</th>
                    <th scope="col">Acciones</th>
                </tr>
            </thead>
            <tbody>
                <tr v-for="nota in notas" :key="nota._id">
                    <th scope="row">{{nota._id}}</th>
                    <th>{{nota.nombre}}</th>
                    <th>{{nota.descripcion}}</th>
                    <th>
                        <b-button variant="danger" @click="eliminarNota(nota._id)">Eliminar</b-button>
                        <b-button variant="warning" @click="editarNota(nota._id)">Editar</b-button>
                    </th>
                </tr>

            </tbody>

        </table>



        <b-alert :show="dismissCountDown" dismissible @dismissed="dismissCountDown=0"
            @dismiss-count-down="countDownChanged"  :variant="mensaje.color">
            {{mensaje.texto}}
        </b-alert>
    </div>
</template>

<script>
    export default {
        data() {
            return {
                notas: [],
                dismissCountDown: 0,
                mensaje: {
                    color: "success",
                    texto: ""
                },
                nota: {
                    nombre: "",
                    descripcion: ""
                },
                editando: {
                    state: false,
                    id: ""
                }
            }
        },
        methods: {
            // async getNotas() {
            //     try {
            //         const res = await this.axios.get('/nota')
            //         this.notas = res.data
            //     } 
            //     catch (error) {
            //         console.log(error)
            //     }
            // }
            getNotas() {
                this.axios.get('/nota')
                    .then(res => {
                        this.notas = res.data
                    })
                    .catch(e => {
                        console.log(e.response)
                        this.alerta({color:"danger", texto: "ERROR"})
                    })
            },
            getNota(id){
                this.axios.get(`/nota/${id}`)
                    .then(res => {
                        console.log(res)
                        this.nota.nombre = res.data.nombre
                        this.nota.descripcion = res.data.descripcion
                    })
                    .catch(e => {
                        console.log(e.response)
                        this.alerta({color:"danger", texto: "ERROR"})
                    })
            },
            agregarNota() {
                this.axios.post('/nueva-nota', this.nota)
                    .then(res => {
                        this.nota.nombre = ""
                        this.nota.descripcion = ""
                        this.alerta({texto: "Nota creada"})
                        this.getNotas()
                    })
                    .catch(e => {
                        console.log(e.response)
                        if (e.response.data.error.errors.nombre.message) {
                            console.log(e.response.data.error.errors.nombre.message)
                            this.alerta({color:"danger", texto: e.response.data.error.errors.nombre.message})
                        } else {
                            this.alerta({color:"danger", texto: "ERROR"})
                        }
                    })
            },
            eliminarNota(id) {
                this.axios.delete(`/nota/${id}`)
                    .then(res => {
                        this.alerta({color:"warning", texto: "Nota eliminada"})
                        this.getNotas()
                    })
                    .catch(e => {
                        console.log(e.response)
                        this.alerta({color:"danger", texto: "ERROR"})
                    })
            },
            submitEditarNota(id) {
                this.axios.put(`/nota/${id}`, this.nota)
                    .then(res => {
                        this.alerta({color:"success", texto: "Nota editada"})
                        this.nota.nombre = ""
                        this.nota.descripcion = ""
                        this.getNotas()
                        this.editando.state=false
                    })
                    .catch(e => {
                        console.log(e.response)
                        if (e.response.data.error.errors.nombre.message) {
                            console.log(e.response.data.error.errors.nombre.message)
                            this.alerta({color:"danger", texto: e.response.data.error.errors.nombre.message})
                        } else {
                            this.alerta({color:"danger", texto: "ERROR"})
                        }
                        this.editando.state=false
                    })
            },
            editarNota(id) {
                this.getNota(id)
                this.editando.state=true
                this.editando.id = id
            },
            countDownChanged(dismissCountDown) {
                this.dismissCountDown = dismissCountDown
            },
            showAlert() {
                this.dismissCountDown = 5
            },
            alerta(payload) {

                payload.color ? this.mensaje.color = payload.color : this.mensaje.color = 'success'
                payload.texto ? this.mensaje.texto = payload.texto : this.mensaje.texto = 'Prueba Alerta'
                this.showAlert()
            }

        },
        created() {
            this.getNotas()
        }
    }
</script>

